.gallery-first-banner {
	background: var(--url-gallery-banner1) no-repeat center center;
	background-size: cover;
	position: relative;
	max-height: 464px;
	height: 464px;

	.accordion {
		margin: 0 auto;
	}

	@media (max-width: 1340px) {
		height: 100%;
		max-height: 100%;
	}

	&__path {
		padding: 80px 0 0 0;
		font-family: "Questrial";
		font-size: 14px;
		font-weight: 400;
		line-height: 18px;
		letter-spacing: 0em;
		text-align: left;
		color: #ababa7;
		text-transform: uppercase;
		span {
			color: #ababa7;
			opacity: 0.5;
		}
		a {
			color: #ababa7;
		}
	}

	&__title {
		font-family: "Glacior";
		font-size: 72px;
		font-weight: 400;
		line-height: 72px;
		letter-spacing: 0em;
		text-align: left;
		padding-top: 30px;
		color: #ababa7;
		max-width: 843px;
		@media (max-width: 1340px) {
			max-width: 50vw;
			padding-bottom: 60px;
		}

		@media (max-width: 767px) {
			font-size: 40px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0em;
			max-width: 100%;
			//text-align: center;
			padding-top: 14px;
			margin-bottom: 40px;
			padding-bottom: 0;
		}
		@media (max-width: 360px) {
			font-size: 32px;
		}
	}

	&__description {
		position: absolute;
		bottom: 25px;
		float: right;
		right: 8%;
		color: #ababa7;
		max-width: 282px;
		font-family: "Questrial";
		font-size: 14px;
		font-weight: 400;
		line-height: 18px;
		letter-spacing: 0em;
		text-transform: uppercase;

		@media (max-width: 767px) {
			position: relative;
			right: 16px;
			max-width: 192px;
			bottom: unset;
			margin-bottom: 40px;
		}
	}

	.container {
		padding: 0 24px;
		max-width: 1440px;
		margin: auto;

		@media (max-width: 767px) {
			display: table;
			padding: 0 16px;
		}
	}
	.gallery {
		position: absolute;
		left: 24px;
		bottom: 26px;
		color: #ababa7;
		text-transform: uppercase;
	}
	.instagram {
		position: absolute;
		right: 24px;
		bottom: 26px;
		color: #ababa7;
		text-transform: uppercase;
	}
}

.gallery-items {
	padding: 80px 24px 140px 24px;

	.dropbtn.active {
		z-index: 10 !important;
	}

	@media (max-width: 767px) {
		padding: 32px 16px 152px 16px;
	}

	&__mobile {
		&-filter {
			display: none;
			z-index: 999;
			background: #161619 var(--url-noise) repeat center center;
			height: 100%;
			top: 0px;
			bottom: 0;
			right: 0;
			left: 0;
			position: fixed;

			&-back {
				//styleName: Body CAPS 14;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				color: #ababa7;
				display: flex;
				align-items: center;
				margin-top: 16px;
				margin-left: 16px;
				text-transform: uppercase;
			}

			&.active {
				display: unset;
			}

			&-filter {
				display: flex;
				margin-top: 8px;
				//styleName: Body CAPS 14;
				// font-family: "Questrial";
				// font-size: 14px;
				// font-weight: 400;
				// line-height: 18px;
				// letter-spacing: 0em;
				// text-align: left;
				// text-transform: uppercase;
			}

			&-box {
				display: flex;
				flex-direction: column;
				padding: 24px 16px 0 16px;
				overflow: auto;
				// height: calc((var(--vh, 1vh) * 50) - 64);

				&-close {
					text-transform: uppercase;
					align-self: center;
					position: absolute;
					bottom: 16px;
					padding: 16px 63px;
					background: #ababa7;
					border: none;
					font-family: "Questrial";
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
				}

				.accordion.withoutnumber {
					overflow: auto;
					height: calc(calc(var(--vh, 1vh) * 100) - 64px);
					padding-bottom: 64px;
					width: 100%;
				}

				.dropdown-item {
					color: #ababa7;
				}
				a.accordion-title.accordionTitle.js-accordionTrigger.is-collapsed.is-expanded {
					svg {
						transform: rotate(180deg);
					}
				}
				.accordion.withoutnumber .accordionTitle {
					display: flex;
					flex-direction: unset;
					justify-content: flex-start;
				}

				svg {
					margin-right: 4px;
				}

				svg path {
					stroke: #ababa7;
				}

				dd {
					margin: 0;
				}

				.accordion.withoutnumber dl dt:first-child {
					border: unset;
				}

				.accordion dl {
					border: unset;
				}

				.container-checkbox {
					margin-top: 16px;
				}

				.accordion.withoutnumber .accordionTitle {
					width: 100%;
				}
			}
		}
	}

	// @media (min-width: 768px) {
	// 	display: none;
	// }

	// background: #ABABA7;
	background-image: var(--url-greyBg);

	&__goup {
		cursor: pointer;
		position: absolute;
		right: 24px;
		bottom: 56px;
		svg {
			transform: rotate(90deg);
		}
		@media (max-width: 767px) {
			bottom: 48px;
			right: 50%;
			transform: translateX(50%);

			svg {
				width: 64px;
				height: 64px;
			}
		}
		&:hover {
			circle {
				stroke: #161619;
				opacity: 1;
			}
		}
	}

	&__pagination {
		display: flex;
		margin: auto;
		justify-content: center;
		gap: 16px;
		margin-top: 56px;
		div:hover {
			opacity: 1;
		}
		&-arrow {
			align-self: center;
			cursor: pointer;
			&:hover {
				path {
					fill: #161619;
					opacity: 1;
				}
			}
		}
		&-item {
			align-self: center;
			cursor: pointer;
			//styleName: Body 18;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			opacity: 0.5;
			color: #161619;
			&.active {
				opacity: 1;
			}
		}
	}

	&__cards {
		max-width: 1392px;
		margin: auto;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		gap: 24px;
		row-gap: 56px;

		@media (max-width: 1400px) {
		// 	justify-content: space-between;
		// 	gap: 8px;
		}
		@media (max-width: 767px) {
			gap: 40px;
			justify-content: center;
		}
	}

	&__card {
		a {
			display: table-cell;
			white-space: initial !important;
			width: 100%;
			position: initial;
		}
		&:hover {
			.gallery-items__card__title {
				padding-top: 26px;
			}
			.alert {
				&-bottom-right {
					bottom: 36px;
					right: 32px;
				}
				&-bottom-left {
					bottom: 36px;
					left: 32px;
				}
				&-top-right {
					top: 32px;
					right: 32px;
				}
				&-top-left {
					top: 32px;
					left: 32px;
				}
				display: flex;
			}
			.gallery-items__card__title {
				// padding-top: 14.5px;
				// @media (min-width: 1300px) {
				// padding-top: 14px;
				// }
			}
			.gallery-items__card-line-left,
			.gallery-items__card-line-right,
			.gallery-items__card-line-bottom,
			.gallery-items__card-line-topleft,
			.gallery-items__card-line-topright {
				display: unset !important;
			}

			.gallery-items__card-underline {
				display: none;
			}
			.gallery-items__card-img {
				// display: flex;
				// flex-direction: column;
				// justify-content: center;
				// margin: auto;
				// align-items: center;
				img {
					transition: 0.2s;
					margin: 17px 0;
					width: calc(100% - 17px);
					padding: 0 0 0 17px;
					object-fit: revert;
				}
				// transition: 0.1s;
				// padding: 0;
				// height: calc(100% - 32px) !important;
			}
		}
		max-width: 448px;
		cursor: pointer;
		position: relative;
		@media (min-width: 992px) and (max-width: 1440px) {
			width: calc(32.9% - 12px)
		}
		@media (min-width: 768px) and (max-width: 991px) {
			width: calc(50% - 12px);
		}
		// max-height: max-content;
		display: table-column;
		img {
			width: 100%;
			max-height: 448px;
			aspect-ratio: 1;
			object-fit: cover;
		}
		&-underline {
			margin-top: 13px; // 18
			// display: flex;
			// width: 100%;
			display: list-item;

			position: relative;
			bottom: 1px;
			width: calc(100% + 0px);
			height: 1px;
			background: #8c7e66;

			&::after {
				content: " ";
				background: #8c7e66;
				position: absolute;
				height: 5px;
				width: 5px;
				border-radius: 100%;
				right: 0;
				top: -2px;
			}
			&::before {
				content: " ";
				background: #8c7e66;
				position: absolute;
				height: 5px;
				width: 5px;
				border-radius: 100%;
				left: 0;
				top: -2px;
			}
		}

		&__title {
			padding-top: 12px;
			font-family: "Questrial";
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;
			position: relative;
			bottom: 0;

			@media (max-width: 767px) {
				//styleName: Body 18;
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
			}
		}
		&-img {
			position: relative;

			.alert {
				&-bottom-right {
					bottom: 32px;
					right: 16px;
					transform: rotate(15deg);
				}
				&-bottom-left {
					bottom: 32px;
					left: 16px;
					transform: rotate(-15deg);
				}
				&-top-right {
					top: 16px;
					right: 16px;
					transform: rotate(-15deg);
				}
				&-top-left {
					top: 16px;
					left: 16px;
					transform: rotate(15deg);
				}
				transition: 0.2s;
				position: absolute;
				background: #161619;
				width: 120px;
				height: 120px;
				@media (max-width: 1200px) {
					height: 80px;
					width: 80px;
				}
				// padding: 32px;
				font-family: "Questrial";
				font-size: 24px;
				font-weight: 400;
				line-height: 31px;
				letter-spacing: 0em;
				text-align: center;
				color: #ababa7;
				border-radius: 50%;
				overflow: hidden;
				align-self: center;
				display: flex;
				justify-content: center;
				flex-direction: column;
				display: none;
			}

			&:not(.gallery-items__card-img-first) {
				margin-bottom: -20px;
				display: inline-grid;
				width: 100%;
				img {
					// max-height: calc(100% - 20px);
				}
			}
			&-first {
				display: flex;
				flex-direction: column;
				justify-content: center;
				margin: auto;
				align-items: center;
				.gallery-items__card-line-left,
				.gallery-items__card-line-right,
				.gallery-items__card-line-bottom,
				.gallery-items__card-line-topleft,
				.gallery-items__card-line-topright {
					display: unset !important;
				}

				img {
					width: calc(100% - 32px) !important;
					height: calc(100% - 32px) !important;
					margin: 16.5px auto;
				}
				padding: 0;
				// width: calc( 100% - 32px)!important;
				height: calc(100% - 32px) !important;
			}
		}

		&-line {
			&-topleft {
				display: none;
				position: absolute;
				top: 0;
				width: calc(25%);
				left: 0;
				height: 1px;
				background: #8c7e66;
				&::before {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					right: 0;
					top: -2px;
				}
			}
			&-topright {
				display: none;
				position: absolute;
				top: 0;
				width: calc(25%);
				height: 1px;
				right: 0;
				background: #8c7e66;
				&::before {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					left: 0;
					top: -2px;
				}
			}
			&-left {
				position: absolute;
				left: 0;
				top: 0;
				height: calc(100% + 1px);
				width: 1px;
				background: #8c7e66;
				display: none;
			}
			&-right {
				position: absolute;
				right: 0;
				top: 0;
				height: calc(100% + 1px);
				width: 1px;
				background: #8c7e66;
				display: none;
			}
			&-bottom {
				position: absolute;
				// bottom: 0;
				bottom: -1px;
				width: calc(100% + 0px);
				height: 1px;
				background: #8c7e66;
				display: none;
			}
		}
	}

	&__filter {
		display: flex;
		flex-direction: column;
		margin-top: 24px;
		margin-bottom: 48px;
		.button-first {
			color: #161619;
			margin-top: 16px;
			@media (max-width: 767px) {
				margin-top: 26px;
				margin-bottom: 24px;
				display: none;
			}
			&::before {
				background: #161619 !important;
			}
		}

		@media (max-width: 767px) {
			// overflow: auto;
		}
		&-values {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.button-first {
				text-transform: uppercase;
			}

			&-items {
				display: flex;
				gap: 16px;
				margin-top: 24px;
				overflow-x: auto;
				overflow-y: hidden;

				&::-webkit-scrollbar {
					height: 5px;
				}
	
				&::-webkit-scrollbar-track {
					-webkit-box-shadow: inset 0 0 0px rgba(0, 0, 0, 0);
				}
	
				&::-webkit-scrollbar-thumb {
					height: 5px;
					background-color: #656564;
				}
	
				&::-webkit-scrollbar-thumb:hover {
					background-color: #7e7e7c;
				}
	
				&::-webkit-scrollbar:vertical {
					display: none;
				}
			}

			&-item {
				display: flex;
				justify-content: center;
				flex-direction: initial;
				align-items: center;
				cursor: pointer;
				//styleName: Body 18;
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
				flex: none;
			}

			svg {
				padding-left: 8px;
			}
		}

		&-search {
			position: absolute;
			width: calc(100% - 32px);
			background-image: var(--url-greyBg);
			z-index: 1;
			&.active {
				display: unset !important;
				z-index: 999;
				@media (min-width: 768px) {
					max-width: 400px;
					right: 24px;

					input {
						// padding: 0 24px;
					}
				}
			}
			display: none;
			&-box {
				max-height: 150px;
				overflow: hidden;
				z-index: 9999;
				@media (min-width: 768px) {
					padding: 0 24px;
				}
				div {
					color: #161619;
					font-family: 'Questrial';
					font-size: 18px;
					font-weight: 400;
					line-height: 23px;
					letter-spacing: 0em;
					text-align: left;
					margin: 12px 0;

					text-overflow: ellipsis;
					overflow: hidden; 
					white-space: nowrap;
				}
			}

			&-list {
				padding: 4px 16px 16px 16px;
				max-height: 343px;
				margin-top: 16px;
				display: none;
				&.no-results {
					.gallery-items__filter-search-box,
					.gallery-items__filter-search-list__btn {
						display: none;
					}

				}
				&.active {
					display: unset !important;
				}
				&__notFound {
					color: #161619;
					font-family: 'Questrial';
					font-size: 18px;
					font-weight: 400;
					line-height: 23px;
					padding: 0 24px;
					margin-bottom: -16px;
			
					@media (max-width: 767px) {
						padding: 0 16px;
					}
				}
				&__btn {
					//styleName: Body CAPS 14;
					font-family: 'Questrial';
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
					color: #ababa7;
					width: auto;
					background: #161619;
					padding: 16px 0;
					text-align: center;
					text-transform: uppercase;
					display: block;
					@media (min-width: 768px) {
						margin: 0 24px;
					}
				}
			}
			&-btn {
				z-index: 4;
				position: absolute;
				right: 0px;
				top: 0px;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
				border-bottom: 1px solid #161619;
				cursor: pointer;
			}

			input {
				padding: 0px 20px 8px 0;
				&::placeholder {
					color: #5f5f5f;
				}
			}
		}

		&-content {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding: 24px 0;

			@media (min-width: 768px) {
				overflow: auto;
			}

			@media (max-width: 767px) {
				padding: 16px 0;
			}

			&-helpicon {
				@media (min-width: 768px) {
					display: none;
				}
			}

			&-sort {
				white-space: nowrap;
				justify-content: center;
				display: flex;
				align-items: center;

				.dropdowList {
					position: absolute;
					right: 0;
				}

				.dropbtn {
					position: relative;
					top: -3px;
					//styleName: Body CAPS 14;
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					align-items: center;
					text-align: left;
					text-transform: uppercase;
					padding-right: 0;
					margin-right: 0;

				}

				@media (max-width: 767px) {
					.dropbtn {
						margin: 0;
						padding: 0;
					}
				}
			}

			&-mobilebox {
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;

				display: flex;
				align-items: center;
				text-transform: uppercase;
				svg {
					padding-left: 8px;
				}
				@media (min-width: 768px) {
					display: none;
				}
			}

			&-box {
				display: flex;
				flex-direction: row;
				white-space: nowrap;
				text-overflow: ellipsis;

				@media (max-width: 767px) {
					display: none;
					overflow: auto;
				}
				// margin: 0 0 -20px 0;
			}
			@media (min-width: 768px) {
				margin: 0 0px 0 -16px;
			}
		}
		&-line {
			&-bottom {
				position: relative;
				bottom: 1px;
				width: calc(100% + 0px);
				height: 1px;
				background: #8c7e66;

				&::after {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					right: 0;
					top: -2px;
				}
				&::before {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					left: 0;
					top: -2px;
				}
			}
			&-top {
				position: relative;
				bottom: 1px;
				width: calc(100% + 0px);
				height: 1px;
				background: #8c7e66;

				&::after {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					right: 0;
					top: -2px;
				}
				&::before {
					content: " ";
					background: #8c7e66;
					position: absolute;
					height: 5px;
					width: 5px;
					border-radius: 100%;
					left: 0;
					top: -2px;
				}
			}
		}
	}

	&__title {
		font-family: "Glacior";
		font-size: 96px;
		font-weight: 400;
		line-height: 96px;
		letter-spacing: 0em;
		color: #161619;
		position: relative;
		margin: auto;
		text-align: center;

		@media (max-width: 767px) {
			font-size: 50px;
			font-weight: 400;
			line-height: 50px;
			letter-spacing: 0em;
			text-align: center;
		}

		span {
			color: #161619;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			position: absolute;
			top: 10px;
			padding-right: 8px;

			@media (max-width: 915px) {
				padding-right: 8px;
				position: unset;
				text-align: center;
				top: unset;
				bottom: 23px;
				position: absolute;
			}
		}
	}
}

/* Dropdown Button */
.dropbtn {
	margin: 0 8px;
	position: relative;
	background-color: #ffffff00;
	color: #161619;
	// padding: 16px;
	// font-size: 16px;
	border: none;
	cursor: pointer;
	//styleName: Body 18;
	font-family: "Questrial";
	font-size: 18px;
	font-weight: 400;
	line-height: 23px;
	letter-spacing: 0em;
	text-align: left;
	display: flex;
	white-space: nowrap;
}

/* Dropdown button on hover & focus */
.dropbtn:hover,
.dropbtn:focus {
	// background-color: #3e8e41;
}

.dropdowList {
	&::after {
		content: " ";
		background: #ababa7;
		top: -41px;
		position: absolute;
		width: 100%;
		height: 41px;
	}
}
/* The container <div> - needed to position the dropdown content */
.dropdown {
	display: inline-block;

	&-item {
		cursor: pointer;
	}

	.dropbtn.active {
		z-index: 6;
	}

	.dropbtn.active &-icon {
		svg {
			transform: rotate(180deg);
		}
	}

	&-helpicon {
		cursor: pointer;
		//padding-right: 16px;

		@media (max-width: 767px) {
			display: none;
		}
	}
	&-icon {
		display: flex;
		padding-left: 8px;
	}
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
	display: none;
	position: absolute;
	background-color: #ababa7;
	min-width: 230px;
	z-index: 1;
}

.dropdown-content .dropdown-item {
	padding: 8px 16px;
}

/* Links inside the dropdown */
.dropdown-content a {
	color: black;
	padding: 12px 16px;
	text-decoration: none;
	display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
	background-color: #f1f1f1;
}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {
	display: block;
	z-index: 4;
}
