.first-banner {
	height: 100vh;
	background: var(--url-background1) no-repeat center center;
	background-size: cover;
	position: fixed;
	width: 100%;
	z-index: 1;

	.slick-track {
		display: flex;
		gap: 24px;
	}

	&__gohome {
		display: flex;
		cursor: pointer;
		position: absolute;
		bottom: 31px;
		right: 50%;
		transform: translate(50%, 50%);
		max-width: 100vw;
		z-index: 2;

		@media (max-width: 767px) {
			display: none;
		}
		a {
			color: #161616;
		}
		path {
			fill: rgb(171, 171, 167);
		}

	}

	img {
		position: absolute;
		left: 0;
		bottom: 0;
		border-top-right-radius: 60%;
		overflow: hidden;
		@media (max-width: 767px) {
			width: 185px;
			height: 130px;
		}
	}

	&__title {
		font-family: "Glacior";
		font-size: 72px;
		font-weight: 400;
		line-height: 72px;
		letter-spacing: 0em;
		text-align: left;
		padding-top: 128px;
		color: #ababa7;
		max-width: 762px;

		@media (max-width: 767px) {
			font-size: 40px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0em;
			max-width: 100%;
			text-align: left;
		}
		@media (max-width: 360px) {
			font-size: 32px;
		}
	}

	&__description {
		position: relative;
		bottom: 25%;
		float: right;
		// right: 8%;
		color: #ababa7;
		max-width: 282px;
		margin-top: 161px;
		font-family: "Questrial";
		font-size: 14px;
		font-weight: 400;
		line-height: 18px;
		letter-spacing: 0em;
		text-transform: uppercase;
		@media (min-width: 1400px) {
			right: 170px;
		}

		@media (max-width: 767px) {
			position: absolute;
			right: 16px;
			max-width: 192px;
		}
	}

	.container {
		padding: 0 24px;
		opacity: 0;

		&__discover {
			position: absolute;
			padding: 2px;
			width: 324px;
			bottom: 0;
			left: 0;
			transform: translateY(48%) translateX(-22%);

			& svg {
				transform: rotate(30deg);
				stroke-dasharray: 1000px;
				stroke-dashoffset: 1000px;
			}

		}

	}
	.gallery {
		position: absolute;
		left: 24px;
		bottom: 26px;
		color: #ababa7;
		text-transform: uppercase;
	}
	.instagram {
		position: absolute;
		right: 24px;
		bottom: 26px;
		color: #ababa7;
		text-transform: uppercase;
	}
}

.background2 {

	display: flex;
	position: relative;
	align-items: center;
	z-index: 3;
	margin-top: 65vh;
	height: 932px;
	width: 100vw;
	overflow: hidden;
	pointer-events: none;

	@media screen and (max-width: 767px){
		height: auto;
		margin-top: 66vh;
	}

	.container {
		position: relative;
		background-color: #ababa7;
		clip-path: polygon(50% 0, 100% 31%, 100% 100%, 0 100%, 0 31%);
		z-index: 2;
		padding: 104px 0 140px 24px;
		width: 100vw;
		opacity: 0;

		@media (max-width: 767px) {
			padding: 64px 0 80px 0;
			overflow: hidden;
		}
		.button-first {
			text-transform: uppercase;
			position: relative;

			@media (min-width: 768px) {
				margin-bottom: 30px;
			}
			@media (max-width: 767px) {
				bottom: 8px;
			}
		}
	}

	.h1 {
		@media (min-width: 768px) and (max-width: 991px) {
			font-size: 16vw;
		}
		@media (max-width: 767px) {
			font-size: 50px;
			font-weight: 400;
			line-height: 50px;
			letter-spacing: 0em;
			text-align: left;
		}
	}
	&__content {
		padding: 0 24px 26px 0;

		@media (max-width: 767px) {
			padding: 0 16px 26px 16px;
		}
		display: flex;
		justify-content: space-between;
		align-items: flex-end;

		.button-first {
			color: #161619;
			&::before {
				background: #161619 !important;
			}
		}
	}
	.slick-list {
		// margin: 0 -4px;
	}
	.slick-slide {
		margin: 0 24px 0 0;
		@media (max-width: 767px) {
			margin: 0;
			padding: 0 8px 0 16px;
		}
	}
	.slick-slide > div {
		// padding: 0 4px;
	}
	.slick-track {
		// display: flex;
		// gap: 16px;
	}
	&__slide {
		svg {
			position: relative;
			margin-top: 16px;
			width: calc(100% - 8px);
			left: 4px;
			cursor: pointer;

			@media (max-width: 767px) {
				width: calc(100% - 0px);
				left: 0px;
			}
		}
		&-description {
			cursor: pointer;
		}
		&-img {
			margin-top: auto;
			// margin-right: 24px !important;
			cursor: pointer;

			@media (max-width: 767px) {
				canvas {
					width: 285px;
				}
			}

			@media (min-width: 768px) {
				canvas {
					width: 400px;
					max-height: 400px;
				}
			}

			img {
				opacity: 0;
				max-width: 400px;
				max-height: 400px;
			}

			@media (max-width: 767px) {
				margin-right: 0px !important;
			}
		}
		&-title {
			text-align: center;
			margin-top: 8px;
			//styleName: H4;
			font-family: "Glacior";
			font-size: 40px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0em;
			text-align: center;
			cursor: pointer;
			// margin-right: 26px;

			@media (max-width: 767px) {
				font-family: "Glacior";
				font-size: 24px;
				font-weight: 400;
				line-height: 24px;
				letter-spacing: 0em;
				text-align: center;
			}
		}
		a {
			width: initial;
			color: #161619;
			display: contents;
			font-family: unset;
		}
		&-item {
			display: flex !important;
			flex-direction: column;
			max-width: 432px;
			flex: 1 1 auto;
			height: 472px;
			overflow: hidden;
			@media (max-width: 767px) {
				height: 365px;
				width: 309px;
				img {
					max-height: 365px;
					max-width: 309px;
				}
			}
		}
		&-img {
			position: relative;
			div {
				position: absolute;
				top: 0px;
				right: 0px;
				left: 0px;
				bottom: 0px;
				width: calc(100% - 8px);
				overflow: hidden;
				left: 4px;
				@media (max-width: 767px) {
					left: 0;
					width: calc(100% - 0px);
				}
			}
		}
	}
}

