		color: #ababa7;
		@media (max-width: 767px) {
			font-size: 50px;
			font-weight: 400;
			line-height: 50px;
			letter-spacing: 0em;
			text-align: left;
		}
	}

	&__carousel {
		display: flex;
		gap: 16px;
		justify-content: center;
		margin-top: 32px;
		@media (max-width: 767px) {
			flex-wrap: wrap;
			gap: 8px;

			:nth-child(even) {
				order: 1;
			}
			&-item {
				width: calc(50% - 8px) !important;
			}
		}
		&-button {
			text-transform: uppercase;
		}
		&-box {
			position: relative;
			align-items: flex-end;
			display: flex;
			flex-direction: column;
			gap: 16px;
			width: min-content;
			float: inline-end;
			align-self: flex-end;
		}
		&-item {
			display: flex;
			flex-direction: column;
			max-width: 336px;
			max-height: 336px;
			height: 100%;
			width: 100%;
			text-align: end;
			align-items: flex-end;
			gap: 19.7px;

			& svg {
				margin-top: 19.7px;
			}

			a {
				display: contents;
			}
			img {
				max-width: 336px;
				max-height: 336px;
				height: 100%;
				width: 100%;
			}
		}
	}
}

.svg-masked {
	position: relative;
}
