.modal {
	position: fixed;
	background: #ababa7;
	z-index: 991;
	opacity: 1;
	top: 50%;
	right: 50%;
	transform: translate(50%, -50%);
	width: 100%;
	max-width: 688px;

	&-form {
		display: none;
		&.active {
			display: unset;
		}
	}

	@media (max-width: 767px) {
		width: calc(100% - 32px);
		max-width: 400px;
		height: calc((var(--vh, 1vh) * 100) - 32px);
		max-height: calc((var(--vh, 1vh) * 100) - 32px);
	}

	.dropdowList {
		&::after {
			content: unset!important;
		}
	}
	.dropdown {
		width: 100%;
		border-bottom: 1px solid #161619;
		align-self: end;
		padding-bottom: 2px;

		label {
			cursor: pointer;
		}
		&-item {
			overflow: hidden;
		}
		&-input {
            .custom-checkbox, .custom-radio {
                padding-left: 0;
            }
            .custom-checkbox span::before, .custom-radio span::before {
                display: none;
            }
			.dropbtn {
				padding: 0;
				margin: 0;
				width: 100%;
				justify-content: space-between;
				//styleName: Body CAPS 14;
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				text-transform: uppercase;

				&-title {
					z-index: 3;
				}

                &.active {}

				@media (max-width: 767px) {
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
				}
			}
			.dropdowList {
				width: 100%;
			}
		}
	}

	.wrap {
		display: flex;
		gap: 16px;
		@media (max-width: 767px) {
			flex-direction: column;
			display: contents;
		}
		.input-container {
			width: 100%;
		}
	}

	&-container {
		padding: 40px 48px 56px 48px;

		@media (max-width: 767px) {
			padding: 24px 16px;
		}
	}
	&-box {

		&-send {
			height: 100%;
			display: flex;
			flex-direction: column;
			@media (min-width: 768px) {
				display: contents;
			}
			&.hide {
				display: none;
			}
		}

		&-thanks {
			display: none;

			.gallery-items__card {
				pointer-events: none;
			}

			.gallery-items__card-img-first img {
				width: calc(100% - 16px) !important;
				height: calc(100% - 16px) !important;
				margin: 8px auto !important;
			}

			.gallery-items__card {
				display: grid;
				margin: 38.5px auto 38.5px auto;
				width: 100%;
  				max-width: 263px;
				&-img-first {
					height: unset !important;
				}

				@media (max-width: 767px) {
					margin-top: 40px !important;
					max-width: calc(100% - 48px) !important;
					// margin: 40px 24px 0 24px !important;
				}
			}

			&.active {
				display: flex;
				flex-direction: column;
				height: 100%;

				@media (min-width: 768px) {
					display: contents;
				}
			}
		}

		@media (max-width: 600px) {
			overflow: auto;
			height: calc( (var(--vh, 1vh) * 100) - 82px);
		}
		&-agree {
			margin-bottom: 20px;
			a {
				font-family: 'Questrial';
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161616;
				white-space: initial!important;
				text-decoration: underline;
			}
			.custom-checkbox span, .custom-radio span {
				display: inline;
			}
			.custom-checkbox span span, .custom-radio span span {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				text-decoration: underline;
			}
			margin-top: 40px;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			@media (max-width: 767px) {
				margin-top: 24px;
			}
			.custom-checkbox > div::before,
			.custom-checkbox > span::before,
			.custom-radio > div::before,
			.custom-radio > span::before
			{
				opacity: 0.3;
			}
			.custom-checkbox > input:checked + span::before,
			.custom-radio > input:checked + span::before,
			{
				opacity: 1;
			}
		}
		&__fields {
			display: flex;
			flex-direction: column;
			gap: 32px;
			margin-top: 32px;

			@media (max-width: 767px) {
				gap: 24px;
				margin-top: 18px;
			}
		}

		&-btn {
			margin-top: 40px;

			@media (max-width: 767px) {
				margin-top: auto;
			}
		}

		&__title {
			color: #161619;
			//styleName: H3;
			font-family: "Glacior";
			font-size: 72px;
			font-weight: 400;
			line-height: 72px;
			letter-spacing: 0em;
			text-align: center;
			@media (max-width: 767px) {
				font-family: "Glacior";
				font-size: 50px;
				font-weight: 400;
				line-height: 50px;
				letter-spacing: 0em;
				text-align: center;
			}
		}
		&__description {
			margin-top: 16px;
			color: #161619;
			//styleName: Body 24;
			font-family: "Questrial";
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			letter-spacing: 0em;
			text-align: center;

			@media (max-width: 767px) {
				font-size: 18px;
				line-height: 23.4px;
			}
		}
	}
	&-close {
		cursor: pointer;
		position: absolute;
		right: 8px;
		top: 8px;
	}
}
.blur {
	width: 100%;
	height: 100%;
	top: 0px;
	bottom: 0px;
	right: 0px;
	left: 0px;
	position: fixed;
	background-color: #160802;
	opacity: 0.8;
	z-index: 990;
}

textarea {
	background: #ffffff00 !important;
	border: unset !important;
	border-bottom: 1px solid #161619 !important;
	max-height: 54px !important;
	display: inline;
	padding: 8px 20px 8px 0 !important;
	color: #161619;
	border: 0;
	border-bottom: 1px solid #161619;
	font-family: "Questrial";
	font-size: 14px;
	font-weight: 400;
	line-height: 18px;
	text-align: left;
	resize: none;

	@media (max-width: 767px) {
		max-height: 71px !important;
	}
}
textarea::placeholder {
	color: #616161;
	//styleName: Body CAPS 14;
	font-family: "Questrial";
	font-size: 14px;
	font-weight: 400;
	line-height: 18px;
	letter-spacing: 0em;
	text-align: left;
}
input:-webkit-autofill, textarea:-webkit-autofill {
	-webkit-box-shadow: unset!important
}
