.product {
	background-image: var(--url-greyBg);
	overflow: hidden;

	.path {
		color: #161616;
		a {
			color: #161616;
		}
		span {
			color: #161616;
		}
	}

	&__right {
		// width: 50%;
		// min-width: 70vw;
		width: 100%;
		position: relative;
		max-width: 453px;

		@media (max-width: 991px) {
			max-width: 100%;
		}

		&-title {
			//styleName: H4;
			font-family: "Glacior";
			font-size: 40px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;

			@media (max-width: 767px) {
				//styleName: H2 Mob;
				font-family: "Glacior";
				font-size: 24px;
				font-weight: 400;
				line-height: 24px;
				letter-spacing: 0em;
				text-align: left;
			}
		}
		&-description {
			//styleName: Body 18;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;
			margin-top: 32px;

			@media (max-width: 767px) {
				margin-top: 24px;
			}
		}
		&-properties {
			display: flex;
			justify-content: space-between;
			margin-top: 32px;
			@media (max-width: 767px) {
				margin-top: 24px;
			}
			&-item {
			}
			&-title {
				color: #161619;
				//styleName: Body 18;
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				opacity: 0.5;
			}
			&-description {
				//styleName: Body 18;
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
				margin-top: 4px;
			}
		}

		&-info {
		}

		&-btn {
			margin-top: 32px;
			button {
				border: none;
			}

			@media (max-width: 767px) {
				margin-top: 24px;
			}
		}

		&-agree {
			a {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161616;
				white-space: initial !important;
				text-decoration: underline;
			}
			.custom-checkbox span {
				display: inline;
			}
			.custom-checkbox span span {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				text-decoration: underline;
			}
			margin-top: 32px;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;

			//styleName: Body 18;
		}

		&-money {
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
			letter-spacing: 0em;
			text-align: left;
			margin-top: 32px;
			color: #161619;
			text-transform: uppercase;
		}

		&-available {
			margin-top: 32px;
			//styleName: Body 18;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;
			display: flex;
			&-tooltip {
				&-close {
					cursor: pointer;
					position: absolute;
					right: 8px;
					top: 8px;
				}
			}
			svg {
				cursor: pointer;
				padding-right: 8px;
				position: relative;
				width: 32px;
				height: 32px;
				top: -5px;
			}
		}
	}

	&-accarodion {
		position: relative;
		margin-top: 32px;

		&-arrow {
			position: absolute;
			right: 0px;
			top: 0px;
			cursor: pointer;
			&.show {
				svg {
					transform: rotate(180deg);
				}
			}
		}
		&-title {
			//styleName: Body CAPS 14;
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;
			text-transform: uppercase;
		}
		&-text {
			display: flex;
			flex-direction: column;
			gap: 18px;
			margin-top: 32px;
			@media (max-width: 767px) {
				margin-top: 24px;
			}
			p {
				//styleName: Body 18;
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
			}
		}
		&-form {
			display: flex;
			flex-wrap: wrap;
			gap: 24px;
			row-gap: 16px;
			margin-top: 16px;

			svg {
				cursor: pointer;
			}

			.active {
				path,
				circle,
				rect {
					fill: #161619;
					stroke: #161619;
				}
			}
		}
		&-content {
			margin-top: 8px;
			border-top: 1px solid #7e7e7c;

			.dropdown-content {
				min-width: 100%;
			}

			.dropbtn {
				align-items: center;
				&-label {
					&-text {
						padding-bottom: 4px;
						font-family: "Questrial";
						font-size: 18px;
						font-weight: 400;
						line-height: 23px;
						letter-spacing: 0em;
						text-align: left;
						color: #161619;
					}
				}
			}

			&.dropdown-list {
				padding-top: 8px;
				display: flex;
				justify-content: space-evenly;
				gap: 8px;

				.dropdowList::after {
					top: -31px;
				}

				.dropdown.dropdown-input {
					width: 50%;
					.dropbtn {
						width: 100%;
						display: flex;
						justify-content: space-between;
						margin: 0;
					}
					// display: flex;
					// justify-content: center;
				}
			}

			&.hide {
				max-height: 1px;
				overflow: hidden;
			}
		}
		&-box {
			display: flex;
			flex-direction: row;
			gap: 16px;
			margin: 16px 0;
			&-item {
				width: 100%;
				padding: 15px 0;
				border: 1px solid #7e7e7c;
				text-align: center;
				cursor: pointer;

				&.active {
					background: #ababa7;
					border: 1px solid #161619;
				}
			}
		}
		&-item {
			display: flex;
			justify-content: space-between;
			padding: 8px 0;
			border-bottom: 1px solid #7e7e7c;

			&.downoload {
				padding: 16px 0 8px 0;
				.product-accarodion-item-description svg {
					cursor: pointer;
				}
			}

			&-subTitle {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
				margin-top: 8px;
			}

			&.color {
				display: flex;
				flex-wrap: wrap;
				gap: 8px;
				justify-content: flex-start;
				margin: auto;
				padding: 0;
				border-bottom: unset;
				margin: 8px 0px;

				&:last-child {
					margin-bottom: 0;
				}

				div {
					width: 84px;
					height: 84px;
					cursor: pointer;
					background-size: cover;
					background-repeat: no-repeat;

					@media (min-width: 768px) and (max-width: 991px) {
						width: calc(16.8vw - 16px);
						height: calc(16.8vw - 16px);
						min-width: 64px;
						min-height: 64px;
					}
					@media (max-width: 767px) {
						width: calc(20vw - 13px);
						height: calc(20vw - 13px);
					}
					@media (max-width: 400px) {
						width: calc(25vw - 14px);
						height: calc(25vw - 14px);
					}
					&.active {
						border-radius: 50%;
						color: #ababa7;
						font-family: "Questrial";
						font-size: 14px;
						font-weight: 400;
						line-height: 18px;
						letter-spacing: 0em;
						text-align: left;
						text-transform: uppercase;
						text-align: center;
						display: flex;
						justify-content: center;
						flex-direction: column;
					}
				}
			}

			&-title {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: left;
				color: #161619;
				opacity: 0.5;
				width: 50%;
			}
			&-description {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: right;
				color: #161619;
				width: 50%;
			}
		}
	}

	&__last {
		@media (min-width: 992px) {
			display: none;
		}
		background: black;
		videojs-video {
			width: 100%;
			height: 43vw;
			// max-width: 1392px;
			// max-height: 630px;

			@media (max-width: 767px) {
				background-color: #000;
				height: unset;
				aspect-ratio: 1 / 1; /* ⏹ a perfect square */
			}
		}
	}

	&__left {
		width: max-content;
		display: flex;
		flex-direction: column;
		// justify-content: space-between;

		@media (min-width: 992px) {
			overflow: hidden;
			width: 100%;
		}

		&-video {
			display: flex;
			margin-top: 16px;

			@media (max-width: 991px) {
				display: none;
			}

			videojs-video {
				background-color: #000;
				width: 100%;
				padding-bottom: 44.25%;
				// max-width: 1392px;
				// max-height: 630px;

				@media (max-width: 767px) {
					background-color: #000;
					height: unset;
					aspect-ratio: 1 / 1; /* ⏹ a perfect square */
				}
			}
		}

		&-main {
			&-img {
				@media (max-width: 767px) {
					margin: 0 -16px 0 -16px;
					width: 100vw;
				}
				@media (min-width: 768px) and (max-width: 991px) {
					margin: 0 -24px 0 -24px;
					width: 100vw;
				}
				div {
					position: relative;
					// width: max-content;
				}
				img:first-child {
					height: 100%;
					width: 100%;
					// max-height: 367px;
					object-fit: contain;
					@media (max-width: 767px) {
						height: 90.667vw;
						max-height: 540px;
						object-fit: cover;
					}
				}
				a {
					position: absolute;
					bottom: 16px;
					right: 16px;
					width: 24px;
					height: 24px;
					cursor: pointer;

					&:not(:nth-child(2)) {
						display: none;
					}

					@media (max-width: 767px) {
						bottom: 8px;
						right: 8px;
						img {
							height: unset!important;
						}
					}
					img {
						width: 24px;
						height: 24px;
					}
				}
				// img:last-child {
				// 	cursor: pointer;
				// 	position: absolute;
				// 	bottom: 16px;
				// 	right: 16px
				// }
			}
		}
		&-photos {
			overflow: auto;
			margin-top: 16px;
			display: flex;
			flex-direction: row;
			gap: 16px;

			&::-webkit-scrollbar {
				height: 5px;
			}

			&::-webkit-scrollbar-track {
				-webkit-box-shadow: inset 0 0 0px rgba(0, 0, 0, 0);
			}

			&::-webkit-scrollbar-thumb {
				height: 5px;
				background-color: #656564;
			}

			&::-webkit-scrollbar-thumb:hover {
				background-color: #7e7e7c;
			}

			&::-webkit-scrollbar:vertical {
				display: none;
			}

			@media (max-width: 991px) {
				width: calc(100vw - 16px);
				padding-right: 16px;
			}
			@media (min-width: 768px) and (max-width: 991px) {
				padding-right: 24px;
			}
			img {
				cursor: pointer;
				width: 101px;
				height: 101px;
			}
		}
	}
	.container {
		display: flex;
		flex-direction: row;
		padding: 16px 24px 140px 24px;
		gap: 16px;
		justify-content: center;
		max-width: 1440px;
		margin: auto;

		@media (max-width: 991px) {
			flex-direction: column;
			gap: 60px;
		}
		@media (max-width: 767px) {
			padding: 14px 16px 64px 16px;
			gap: 40px;
		}
	}
}

.product__right-available-tooltip {
	opacity: 0;
	z-index: -1;
	width: 100%;
	position: relative;
	padding: 24px 16px 24px 16px;
	background: #2c2c32;
	//styleName: Body 18;
	font-family: "Questrial";
	font-size: 18px;
	font-weight: 400;
	line-height: 23px;
	letter-spacing: 0em;
	text-align: left;
	color: #ababa7;
}

.product__right-available-tooltip.active {
	opacity: 1 !important;
	z-index: 3;
}

.product-similar {
	background-image: var(--url-greyBg);

	.gallery-items__cards {
		margin-top: 32px;
	}
	&__title {
		font-family: "Glacior";
		font-size: 96px;
		font-weight: 400;
		line-height: 96px;
		letter-spacing: 0em;
		text-align: left;
		color: #161619;

		@media (max-width: 767px) {
			//styleName: H1 Mob;
			font-family: "Glacior";
			font-size: 50px;
			font-weight: 400;
			line-height: 50px;
			letter-spacing: 0em;
			text-align: left;
		}
	}
	.container {
		display: flex;
		flex-direction: row;
		padding: 0px 24px 152px 24px;
		gap: 16px;
		justify-content: center;
		max-width: 1440px;
		margin: auto;
	}
}
