.gallery .header {
	.header__content a {
		&::before {
			position: absolute;
			left: -5px;
		}
		&::after {
			position: absolute;
			right: -5px;
		}
	}

	.header__content {
		width: 100%;
	}
	.header__content-body {
		transition: all 1s;
		flex-direction: row !important;
		width: 100%;
		justify-content: center;
		gap: 40px;
	}
}

.header {
	position: fixed;
	z-index: 990;
	width: 100%;
	&.fixed {
		.header__content {
			width: 100%;
			a {
				&::before {
					position: absolute;
					left: -5px;
				}
				&::after {
					position: absolute;
					right: -5px;
				}
			}
		}
		.header__content-body {
			transition: all 1s;
			flex-direction: row !important;
			width: 100%;
			justify-content: center;
			gap: 40px;
		}
	}
	&-dark {
		.header__mobile rect {
			color: #000 !important;
			fill: #000 !important;
		}
		.header__content-become {
			&::before {
				background: #000 !important;
			}
		}
		.header__logo {
			span,
			div,
			path,
			a {
				color: #000 !important;
				fill: #000 !important;
			}
		}
		span,
		div,
		path,
		a {
			@media (min-width: 767px) {
				color: #000 !important;
				fill: #000 !important;
			}
		}
	}
	.container {
		display: flex;
		justify-content: space-between;
		padding: 16px 24px 16px 24px;
		// max-width: 1440px;
		margin: auto;

		@media (max-width: 767px) {
			display: flex;
			padding: 16px;
		}
	}
	&__mobile {
		display: none;

		&-content {
			&.active {
				display: unset;
			}
			z-index: 2;
			display: none;
			background: #161619 var(--url-noise) repeat center center;
			height: 100%;
			top: 0px;
			bottom: 0;
			right: 0;
			left: 0;
			position: fixed;

			&-button {
				position: fixed !important;
				left: 16px;
				bottom: 26px;
				color: #ababa7;

				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				color: #ababa7 !important;

				img {
					position: fixed;
					bottom: -50px;
					left: -37px;
					width: 56%;
					height: 21%;
				}
			}

			&-box {
				top: 50vh;
				transform: translateY(-50%);
				position: relative;
				gap: 40px;
				display: flex;
				flex-direction: column;
				margin: auto;
				width: 100%;

				div,
				a {
					position: relative;
					font-family: "Glacior";
					font-size: 50px;
					font-weight: 400;
					line-height: 50px;
					letter-spacing: 0em;
					text-align: right;
					color: #ababa7;
					&:nth-child(2) {
						text-align: left;
					}
					@media (max-width: 550px) {
						font-size: 44px;
					}
				}

				div:nth-child(1) {
					padding-left: 30px;
					&:after {
						content: attr(data-number);
						float: left;
						position: absolute;
						left: 0;
						top: 0;
						//styleName: Body CAPS 14;
						font-family: "Questrial";
						font-size: 14px;
						font-weight: 400;
						line-height: 18px;
						letter-spacing: 0em;
						text-align: center;
						color: #ababa7;
					}
				}
				div:nth-child(2) {
					&:after {
						content: attr(data-number);
						float: left;
						position: absolute;
						right: 0;
						top: 0;
						//styleName: Body CAPS 14;
						font-family: "Questrial";
						font-size: 14px;
						font-weight: 400;
						line-height: 18px;
						letter-spacing: 0em;
						text-align: center;
						color: #ababa7;
					}
				}
				div:nth-child(3) {
					padding-left: 30px;
					&:after {
						content: attr(data-number);
						float: left;
						position: absolute;
						left: 0;
						top: 0;
						//styleName: Body CAPS 14;
						font-family: "Questrial";
						font-size: 14px;
						font-weight: 400;
						line-height: 18px;
						letter-spacing: 0em;
						text-align: center;
						color: #ababa7;
					}
				}
			}

			&__logo {
				position: absolute;
				display: flex;
				justify-content: space-between;
				width: calc(100% - 32px);
			}
		}

		@media (max-width: 767px) {
			display: contents;
		}
	}

	&__content {
		width: 452px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 767px) {
			display: none;
		}

		&-column {
			display: flex;
			cursor: pointer;
		}

		&-body {
			display: flex;
			flex-direction: column;
		}

		a:hover {
			&::after {
				content: "]";
				color: #8c7e66;
				//styleName: Body CAPS 14;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				// position: absolute;
				// right: px;
			}
			&::before {
				content: "[";
				color: #8c7e66;
				//styleName: Body CAPS 14;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				// position: absolute;
				// left: -5px;
				// left: 5px;
			}
		}

		span,
		a {
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
			letter-spacing: 0em;
			text-align: left;
			color: #ababa7;
			text-transform: uppercase;
			cursor: pointer;

			&.active {
				color: #8c7e66 !important;
				&::after {
					content: "]";
					color: #8c7e66;
					//styleName: Body CAPS 14;
					font-family: "Questrial";
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
				}
				&::before {
					content: "[";
					color: #8c7e66;
					//styleName: Body CAPS 14;
					font-family: "Questrial";
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
				}
			}

			&:hover {
				color: #8c7e66 !important;
			}
		}

		&-become {
			height: min-content;
			position: relative;
			white-space: nowrap;
			&:hover {
				color: #8c7e66;
				&::before {
					background: #8c7e66 !important;
				}
			}

			&::before {
				content: " ";
				position: absolute;
				background: #ababa7;
				width: 100%;
				height: 1px;
				bottom: -2px;
			}
		}
	}
}
