.privacy-policy {
	background-image: var(--url-greyBg);

	&__goup {
		cursor: pointer;
		position: absolute;
		right: 24px;
		bottom: 56px;

		@media (min-width: 768px) and (max-width: 1200px) {
			bottom: 24px;
		}
		svg {
			transform: rotate(90deg);
		}
		@media (max-width: 767px) {
			bottom: 48px;
			right: 50%;
			transform: translateX(50%);

			svg {
				width: 64px;
				height: 64px;
			}
		}
		&:hover {
			circle {
				stroke: #161619;
				opacity: 1;
			}
		}
	}
    .path {
        color: #161616;
		a {
			color: #161616;
		}
        span {
            color: #161616;
        }
    }

	.container {
		max-width: 1440px;
		padding: 0 24px 140px 24px;
		margin: auto;
	}

	&-title {
		//styleName:2 H2;
		font-family: "Glacior";
		font-size: 96px;
		font-weight: 400;
		line-height: 96px;
		letter-spacing: 0em;
		text-align: left;
		color: #131313;

		@media (min-width: 768px) and (max-width: 991px) {
			font-size: 60px;
			line-height: 60px;
		}

		@media (max-width: 767px) {
			font-size: 50px;
			font-weight: 400;
			line-height: 50px;
			letter-spacing: 0em;
			text-align: center;
		}
	}

	&__box {
		display: flex;
		gap: 104px;

		@media (min-width: 768px) and (max-width: 991px) {
			gap: 36px;
		}

		@media (max-width: 767px) {
			display: block;
		}

		&-title {
			display: flex;
			gap: 104px;
			margin-top: 9px;
			margin-bottom: 64px;
			width: 100%;

			@media (min-width: 768px) and (max-width: 991px) {
				gap: 36px;
			}

			@media (max-width: 767px) {
				display: block;
				margin: 30px 0 32px 0;
			}

			.privacy-policy__box__right {
				max-width: 100%;
			}
		}

		&__left {
			display: flex;
			flex-direction: column;
			gap: 16px;
            @media (min-width: 768px) {
                min-width: 248px;
                max-width: 248px;
				position: sticky;
				top: 80px;
            }

			&-accarodion {
				position: relative;
                margin-bottom: 24px;

				&-arrow {
					position: absolute;
					right: 0px;
					top: -2px;
					cursor: pointer;
                    @media (min-width: 768px) {
                        display: none;
                    }
					&.show {
						svg {
							transform: rotate(180deg);
						}
					}
				}
				&-title {
					//styleName: Body CAPS 14;
					font-family: "Questrial";
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
					color: #39393C;
					text-transform: uppercase;
                    padding-right: 30px;
					cursor: default;

                    @media (max-width: 767px) {
                        color: #131313;
                    }
				}
				&-header {
				}
				&-content {
					margin-top: 8px;
                    @media (max-width: 767px) {
					  border-top: 1px solid #161619;
                    }
					&.hide {
						max-height: 1px;
						overflow: hidden;
					}
				}
				&-box {
					display: flex;
					flex-direction: row;
					gap: 16px;
					margin: 16px 0;
					&-item {
						width: 100%;
						padding: 15px 0;
						text-align: center;
						cursor: pointer;
					}
				}

				&-item {
					display: flex;
					justify-content: space-between;
					padding: 8px 0;
					cursor: pointer;
					font-family: "Questrial";
					font-size: 14px;
					font-weight: 400;
					line-height: 18px;
					letter-spacing: 0em;
					text-align: left;
					color: #161616;
					text-transform: uppercase;

					&:hover {
						color: #8c7e66;
					}

                    @media (min-width: 768px) {
                        max-width: 248px;
                    }
				}
			}

			// :first-child {
			// 	color: #39393c;
			// }
			// div {
			// 	max-width: 248px;
			// 	cursor: pointer;
			// 	font-family: "Questrial";
			// 	font-size: 14px;
			// 	font-weight: 400;
			// 	line-height: 18px;
			// 	letter-spacing: 0em;
			// 	text-align: left;
			// 	color: #161616;
			// 	text-transform: uppercase;
			// }
		}

		&__right {
			display: flex;
			flex-direction: column;
			gap: 80px;
			max-width: 688px;

			@media (max-width: 767px) {
				gap: 56px;
			}

			&-item {
				display: flex;
				flex-direction: column;
				gap: 24px;

				@media (max-width: 767px) {
					gap: 16px;
				}

				:last-child {
					margin-bottom: 0;
				}

				* {
					font-family: "Questrial";
				}

				a {
					color: #161616;
					white-space: normal;
				}

				li {
					display: list-item;
					padding-inline-start: 32px;
					position: relative;
					margin-bottom: 9px;

					//styleName: Body 18;
					font-family: "Questrial";
					font-size: 18px;
					font-weight: 400;
					line-height: 23px;
					letter-spacing: 0em;
					text-align: left;

					&::before {
						content: url("../img/li-icon.svg");
						position: absolute;
						width: 20px;
						height: 20px;
						left: 0px;
						top: -2px;
						opacity: 0.3;
					}
					// list-style-type: url("../img/li-icon.svg");
				}

				ol {
					list-style: auto;
					right: -20px;
					position: relative;
					width: calc(100% - 20px);
					li {
						padding-inline-start: 8px;
						&::before {
							display: none;
						}

						&::marker {
							position: absolute;
							left: 10px;
						}
					}
				}

				h2 {
					font-size: 40px;
					font-weight: 400;
					line-height: 31px;
					letter-spacing: 0em;
					text-align: left;
					color: #161619;
					position: relative;
					bottom: 0;
					@media (max-width: 767px) {
						font-size: 24px;
						line-height: 50px;
						font-weight: 400;
					}
				}

				h3 {
					font-size: 36px;
					font-weight: 400;
					line-height: 31px;
					letter-spacing: 0em;
					text-align: left;
					color: #161619;
					position: relative;
					bottom: 0;
					@media (max-width: 767px) {
						font-size: 22px;
						line-height: 50px;
						font-weight: 400;
					}
				}

				h4 {
					font-size: 34px;
					font-weight: 400;
					line-height: 31px;
					letter-spacing: 0em;
					text-align: left;
					color: #161619;
					position: relative;
					bottom: 0;
					@media (max-width: 767px) {
						font-size: 22px;
						line-height: 50px;
						font-weight: 400;
					}
				}

				h5 {
					font-size: 32px;
					font-weight: 400;
					line-height: 31px;
					letter-spacing: 0em;
					text-align: left;
					color: #161619;
					position: relative;
					bottom: 0;
					@media (max-width: 767px) {
						font-size: 22px;
						line-height: 50px;
						font-weight: 400;
					}
				}

				p {
					//styleName: Body 18;
					font-family: "Questrial";
					font-size: 18px;
					font-weight: 400;
					line-height: 23px;
					letter-spacing: 0em;
					text-align: left;
					color: #161616;

					@media (max-width: 767px) {
						font-size: 18px;
						font-weight: 400;
						line-height: 23px;
						letter-spacing: 0em;
						text-align: left;
					}
				}
			}

			&-title {
				//styleName: H4;
				font-family: "Glacior";
				font-size: 40px;
				font-weight: 400;
				line-height: 40px;
				letter-spacing: 0em;
				text-align: left;
				color: #161616;
				// margin-bottom: 24px;s

				@media (max-width: 767px) {
					font-size: 24px;
					font-weight: 400;
					line-height: 24px;
					letter-spacing: 0em;
					text-align: left;
				}
			}
		}
	}
}
