	position: relative;
	display: flex;
	flex-direction: initial;
	justify-content: flex-end;
	border-top: 1px solid #39393c;
}

.accordion.withoutnumber {
	.accordionTitle {
		width: calc(100% - 32px);
		// background: red;
		&:after {
			display: none !important;
		}
		&::before {
			display: none !important;
		}
	}
}

.accordion dl {
	border-bottom: 1px solid #39393c;
}

.accordion dd,
.accordion__panel {
	padding-bottom: 20px;
}
.accordion p {
	@media (min-width: 768px) {
		width: 688px;
	}
	float: right;
	//styleName: Body 18;
