}

.accordion {
	position: relative;
}

.accordionTitle,
.accordion__Heading {
	position: initial;
	padding: 20px 0 0 0;
	max-width: 688px;
	justify-content: space-between;
	//display: flex;
	flex-direction: row-reverse;
	background-color: #ffffff00;
	width: 688px;
	//float: right;
	text-align: center;
	font-weight: 700;
	text-decoration: none;
	color: #fff;
	transition: background-color 0.5s ease-in-out;
	border-bottom: 1px solid darken(#ffffff00, 5%);

	@media (min-width: 992px) {
		margin-right: 121px;
		font-size: 40px;
	}

	@media (max-width: 767px) {
		word-wrap: break-word;
		word-break: break-all;
		white-space: normal;
		display: block;
		width: calc(100% - 110px);
		margin: auto;
	}

	//styleName: H4;
	font-family: "Glacior";
	font-size: 24px;
	font-weight: 400;
	line-height: 24px;
	letter-spacing: 0em;
	text-align: left;
	color: #ababa7;

	&:after {
		content: attr(data-number);
		float: left;
		transition: transform 0.3s ease-in-out;
		position: absolute;
		left: 0;
		//styleName: H4;
		font-family: "Glacior";
		font-size: 40px;
		font-weight: 400;
		line-height: 40px;
		letter-spacing: 0em;
		text-align: left;
		position: absolute;
		left: 0;
		right: 0;
		bottom: -8px;

		@media (max-width: 767px) {
			font-size: 24px;
			font-weight: 400;
			line-height: 24px;
			letter-spacing: 0em;
			text-align: left;
			bottom: unset;
			top: 20px;
		}
	}
	&:before {
		content: "+";
		right: 0;
		position: absolute;
		font-size: 1.5em;
		line-height: 0.5em;
		float: right;
		transition: transform 0.3s ease-in-out;
	}
	&:hover {
		background-color: darken(#ffffff00, 10%);
	}
}
.accordionTitleActive,
.accordionTitle.is-expanded {
	background-color: darken(#ffffff00, 10%);
	&:before {
		transform: rotate(-225deg);
	}
}
.accordionItem {
	height: auto;
	overflow: hidden;
	//SHAME: magic number to allow the accordion to animate
	opacity: 0;

	max-height: 150em;
	transition: max-height 0s;

	@media screen and (min-width: 48em) {
		max-height: 15em;
		transition: max-height 0.5s;
	}
}

.accordionItem.is-collapsed {
	max-height: 0;
}
.no-js .accordionItem.is-collapsed {
	max-height: auto;
}
.animateIn {
	animation: accordionIn 0.45s normal ease-in-out both 1;
	opacity: 1;
}
.animateOut {
	animation: accordionOut 0.45s alternate ease-in-out both 1;
}
@keyframes accordionIn {
	0% {
		opacity: 0;
		transform: scale(0.9) rotateX(-60deg);
		transform-origin: 50% 0;
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}

@keyframes accordionOut {
	0% {
		opacity: 1;
		transform: scale(1);
	}
	100% {
		opacity: 0;
		transform: scale(0.9) rotateX(-60deg);
	}
}

