// checkbox

.container-checkbox {
	position: relative;
	// display: block;
	min-width: 100%;
	z-index: 1;
	position: relative;
	// display: inline-block;
}

.custom-checkbox>input:focus:not(:checked)+span::before {
	border-color: #161619 !important;
}
.container-checkbox .custom-checkbox span::before {
	border: 1px solid #8c7e66;
}
input[type="checkbox"],
.custom-checkbox>div::before, .custom-checkbox>span::before {
  user-select: none !important;
  outline: unset !important;
  box-shadow: none !important;
}

.custom-radio {
	display: block;
	margin-bottom: 0;
	position: relative;
	cursor: pointer;
}
.custom-checkbox {
	display: block;
	margin-bottom: 0;
	padding-left: 28px;
	position: relative;
}
.container-checkbox {
	display: flex;
	flex-direction: column;
	gap: 20px;
}

.container-checkbox .custom-checkbox > input:checked + span::before {
	// 	background-image: var(--url-greyBg);
	background-image: var(--url-checked-black);
	// background-image: var(--url-greyBg);
	background-color: #8c7e66;
	background-size: 80% 80%;
}

.container-checkbox .custom-checkbox > span::before {
	height: 20px;
	width: 20px;
}
.modal-box-agree .custom-checkbox>input:checked+span::before,
.custom-checkbox>input:focus:not(:checked)+span::before{
    outline: none !important;
    border-color: inherit;
    box-shadow: none;
}

/* для элемента input c type="checkbox" */
.custom-checkbox > input {
	position: absolute;
	z-index: -1;
	opacity: 0;
	outline: none !important;
	border-color: inherit;
	-webkit-box-shadow: none;
	box-shadow: none;
	&:focus{
		outline: none !important;
		border-color: inherit;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	*:focus {
		outline: none !important;
		border-color: inherit;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
}

/* для элемента label, связанного с .custom-checkbox */
.custom-checkbox > span {
	display: inline-flex;
	align-items: center;
	user-select: none;
	cursor: pointer;
	width: 100%;
	display: flex;
}

/* создание в label псевдоэлемента before со следующими стилями */
.custom-checkbox > div::before,
.custom-checkbox > span::before {
	content: "";
	display: inline-block;
	width: 20px;
	height: 20px;
	flex-shrink: 0;
	flex-grow: 0;
	border: 1px solid #161619;
	// border-radius: 0.25em;
	margin-right: 10px;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 50% 50%;
	position: absolute;
	left: 0px;
	top: 0px;
}

/* стили при наведении курсора на checkbox */
.custom-checkbox > input:not(:disabled):not(:checked) + span:hover::before {
	// border-color: #b3d7ff;
}

/* стили для активного чекбокса (при нажатии на него) */
.custom-checkbox > input:not(:disabled):active + span::before {
	background-color: #b3d7ff;
	border-color: #b3d7ff;
}

/* стили для чекбокса, находящегося в фокусе */
.custom-checkbox > input:focus + span::before {
	box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

/* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
.custom-checkbox > input:focus:not(:checked) + span::before {
	border-color: #80bdff;
}

/* стили для чекбокса, находящегося в состоянии checked */
.custom-checkbox > input:checked + span::before {
	border-color: #161619;
	background-color: #161619;
	background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

/* стили для чекбокса, находящегося в состоянии disabled */
.custom-checkbox > input:disabled + span::before {
	background-color: #e9ecef;
}

