.loading-div {
	z-index: 99999;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #161619 var(--url-noise) repeat center center;
	display: none;
	&.active {
		display: unset!important;
	}

	&__loading {
		position: absolute;
		bottom: 24px;
		left: 24px;

		font-family: "Questrial";
		font-size: 48px;
		font-weight: 400;
		line-height: 62px;
		letter-spacing: 0em;
		text-align: left;
		color: #ABABA7;

		@media (max-width: 767px) {
			font-size: 28px;
			line-height: 28px;
		}
	}
	&__proccent {
		position: absolute;
		bottom: 24px;
		right: 24px;
		//styleName: H1;
		font-family: "Glacior";
		font-size: 190px;
		font-weight: 400;
		line-height: 190px;
		letter-spacing: 0em;
		text-align: right;
		color: #ABABA7;

		@media (max-width: 767px) {
			font-size: 34px;
			line-height: 34px;
		}
	}
	&__img {
		position: absolute;
		top: 24px;
		left: 24px;
	}
}


