.fadein {
	transform: translate(0, 50px);
	transition: 1s;
}

body.modal-open {
    overflow: hidden;
}
.goup_scroll {
	cursor: pointer;
	position: absolute;
	right: 24px;
	bottom: 56px;

	@media (min-width: 768px) and (max-width: 1200px) {
		bottom: 24px;
	}
	svg {
		transform: rotate(90deg);
	}
	@media (max-width: 767px) {
		bottom: 48px;
		right: 50%;
		transform: translateX(50%);

		svg {
			width: 64px;
			height: 64px;
		}
	}
	&:hover {
		circle {
			stroke: #161619;
			opacity: 1;
		}
	}
}
