.background-map-contact-us {
	background: #161619 var(--url-noise) repeat center center;
	min-height: 100px;
	width: 100%;
	padding: 0 0 140px 0;

	@media (max-width: 767px) {
		padding: 0 0 120px 0;
	}

	&__left {
		display: flex;
		flex-direction: column;
		//justify-content: space-evenly;
		align-items: center;

		svg {
			width: 100%;
		}

		&-title {
			color: #ababa7;
			margin-top: 32px;
			//styleName: H2;
			font-family: "Glacior";
			font-size: 96px;
			font-weight: 400;
			line-height: 96px;
			letter-spacing: 0em;
			text-align: center;

			@media (max-width: 767px) {
				font-family: "Glacior";
				font-size: 50px;
				font-weight: 400;
				line-height: 50px;
				letter-spacing: 0em;
				text-align: center;
			}
		}

		&-description {
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
			letter-spacing: 0em;
			text-align: center;
			color: #ababa7;
			margin: 32px 0 40px 0;
			text-transform: uppercase;
		}

		&-bottom {
			max-width: 400px;
			text-align: center;

			&-title {
				color: #ababa7;
				margin: 40px 0 0 0;
				//styleName: H4;
				font-family: "Glacior";
				font-size: 40px;
				font-weight: 400;
				line-height: 40px;
				letter-spacing: 0em;
				text-align: center;

				@media (max-width: 767px) {
					//styleName: H2 Mob;
					font-family: "Glacior";
					font-size: 24px;
					font-weight: 400;
					line-height: 24px;
					letter-spacing: 0em;
					text-align: center;
				}
			}
			&-btn {
				margin: auto;
				color: #ababa7;
			}
			&-description {
				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: center;
				color: #ababa7;
				margin: 24px 0 40px 0;
			}
		}
	}

	&__right {
		display: contents;
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		img {
			min-width: max-content;
			max-height: max-content;
			@media (max-width: 1400px) {
				min-height: revert;
				min-width: revert;
			}
		}
	}

	.container {
		padding: 54px 24px 0 24px;
		display: flex;
		gap: 16px;
		overflow: hidden;

		max-width: 1440px;
 	    margin: auto;
        flex-direction: row-reverse;

		@media (max-width: 767px) {
			padding: 14px 16px 0 16px;
			flex-direction: column-reverse;
		}
	}
}

.form-send-contact-us {
	background-image: var(--url-greyBg);
	padding: 140px 24px;
	overflow: hidden;
	@media (max-width: 767px) {
		padding: 64px 16px 0 16px;
	}
	&__box {
        &-line {
            &-topleft {
                position: absolute;
                top: 0;
                width: calc(25%);
                left: -8px;
                height: 1px;
                background: #8c7e66;
                @media (min-width: 768px) {
                    display: none;
                }
                &::before {
                    content: " ";
                    background: #8c7e66;
                    position: absolute;
                    height: 5px;
                    width: 5px;
                    border-radius: 100%;
                    right: 0;
                    top: -2px;
                }
            }
            &-topright {
                position: absolute;
                top: 0;
                width: calc(25%);
                height: 1px;
                right: -8px;
                background: #8c7e66;
                @media (min-width: 768px) {
                    display: none;
                }
                &::before {
                    content: " ";
                    background: #8c7e66;
                    position: absolute;
                    height: 5px;
                    width: 5px;
                    border-radius: 100%;
                    left: 0;
                    top: -2px;
                }
            }
            &-left {
                position: absolute;
                left: -8px;
                top: 0px;
                height: calc(100% + 16px);
                width: 1px;
                background: #8c7e66;
                @media (min-width: 768px) {
                    display: none;
                }
            }
            &-right {
                position: absolute;
                right: -8px;
                top: 0px;
                height: calc(100% + 16px);
                width: 1px;
                background: #8c7e66;
                @media (min-width: 768px) {
                    display: none;
                }
            }
            &-bottom {
                position: absolute;
                bottom: -16px;
                left: -8px;
                width: calc(100% + 16px);
                height: 1px;
                background: #8c7e66;
                @media (min-width: 768px) {
                    display: none;
                }
            }
        }
    }
	.container {
		display: flex;
		gap: 104px;
		justify-content: center;

		@media (max-width: 767px) {
			gap: 40px;
		}

		@media (min-width: 1200px) and (max-width: 1400px) {
			gap: 40px;
			.form-send-contact-us__left-title {
				font-size: 120px;
				line-height: 120px;
				margin-top: 144px;
			}
			svg {
				width: 500px;
			}
		}

		@media (max-width: 1200px) {
			flex-direction: column;
			align-items: center;
		}
	}
	&__right {
		display: flex;
		max-width: 571px;
		flex-direction: column;
		@media (min-width: 768px) {
			margin-top: 112px;
		}

		@media (max-width: 767px) {
			justify-content: center;
		}

		@media (max-width: 1400px) {
			max-width: 719px;
		}

		&-title {
			font-family: "Questrial";
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			letter-spacing: 0em;
			text-align: left;
			color: #161619;
		}
		&-button {
			cursor: pointer;
			width: 100%;
			padding: 16px 10px;
			background-color: #161619;
			color: #ababa7;
			text-align: center;
			align-content: center;
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
			letter-spacing: 0em;
			text-transform: uppercase;
			margin-top: 48px;
			border: none;
			@media (max-width: 767px) {
				margin-top: 24px;
			}
		}
		&-form {
			display: flex;
			flex-direction: column;
			gap: 32px;
			padding: 72px 0 80px 0;

			

			@media (max-width: 767px) {
				padding: 56px 0 80px 0;
			}
		}
		.wrap {
			display: flex;
			gap: 24px;
			@media (max-width: 767px) {
				flex-direction: column;
			}
			.input-container {
				width: 100%;
			}
		}
	}
	&__left {
		position: relative;
		&-button {
			cursor: pointer;
			position: absolute;
			right: 17px;
			bottom: 17px;
			@media (max-width: 767px) {
				display: none;
			}
		}
		&-box {
			@media (max-width: 767px) {
				min-width: calc(100vw - 48px);
			}
			position: relative;
			// display: inline;
			@media (max-width: 767px) {
				svg {
					height: auto;
					display: none;
				}
			}
			@media (max-width: 991px) {
				svg {
					max-width: 100%;
				}
			}
		}
		&-title {
			position: absolute;
			margin-top: 34px;
			width: 100%;
			font-family: "Glacior";
			font-size: 190px;
			font-weight: 400;
			line-height: 190px;
			letter-spacing: 0em;
			text-align: left;

			@media (max-width: 767px) {
				font-size: 25vw;
				line-height: 25vw;
				margin-top: 4%;
				position: initial;
			}

			span {
				display: table;
				margin: 0 auto;
				width: min-content;
			}
		}
	}
}

