.footer {
	position: relative;
	overflow: hidden;
	z-index: 3;
	background: #161619 var(--url-noise) repeat center center;
	padding: 0 24px;

	@media (max-width: 767px) {
		padding: 84px 16px 0 16px;
	}

	.container {
		max-width: 1392px;
		margin: auto;

		@media (max-width: 767px) {
			padding: 0 0;
		}
	}

	&__undertext {
		display: flex;
		justify-content: space-between;
		// text-transform: uppercase;
		color: #ababa7;
		padding: 48px 0 26px 0;
		div {
			font-family: "Questrial";
			font-size: 14px;
			font-weight: 400;
			line-height: 18px;
		}
		@media (max-width: 767px) {
			flex-direction: column;
			gap: 24px;

			div {
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
			}
		}
	}

	&__logo {
		margin-top: 104px;
		overflow: hidden;
		font-family: "Glacior";
		font-size: 330px; //330
		font-weight: 400;
		line-height: 330px;
		letter-spacing: 0em;
		text-align: center;
		color: #39393c;
		margin: 0 -24px;
		position: relative;

		@media (max-width: 1430px) {
			font-size: 21vw;
			line-height: 21vw;
			margin: 0 -24px;
		}

		@media(max-width: 767px) {
			margin-top: 96px;
			line-height: unset;
			margin: 0 -16px;
		}

		&-line {
			position: relative;
			margin: 24px 0 48px 0;
			display: flex;
			justify-content: center;

			&-linehider {
				position: absolute;
				background: #161619 var(--url-noise) repeat center center;
				width: 100%;
				height: 2rem;
			}

			@media (max-width: 767px) {
				margin: 16px 0 40px 0;
			}
		}

	}
	&__box {
		display: flex;
		justify-content: center;
		@media (max-width: 767px) {
			flex-wrap: wrap;
			justify-content: space-around;
		}
		&-item {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			color: #ababa7;
			margin-right: auto;

			@media (max-width: 767px) {
				width: 100%;
			}
		}
		&-shop {
			a {
				align-self: initial !important;
			}
			margin-right: 136px;
			@media (min-width: 768px) and (max-width: 991px) {
				margin-right: 32px;
			}
			@media (max-width: 767px) {
				margin-right: 0;
			}
		}
		&-shop,
		&-information {
			display: flex;
			flex-direction: column;
			a {
				align-self: initial !important;
			}

			@media (min-width: 1200px) and (max-width: 1400px) {
				margin-right: 92px;
			}
			@media (min-width: 1401px) {
				margin-right: 100px;
			}

			&-title {
				font-family: "Glacior";
				font-size: 40px;
				font-weight: 400;
				line-height: 40px;
				letter-spacing: 0em;
				text-align: left;
				margin-bottom: 24px;

				@media (min-width: 768px) and (max-width: 991px) {
					font-size: 32px;
					margin-right: auto;
				}
				@media (max-width: 767px) {
					font-size: 24px;
					font-weight: 400;
					line-height: 24px;
					letter-spacing: 0em;
					text-align: left;
				}
			}
			&-categoires {
				display: grid;
				flex-direction: column;
				gap: 8px;
				//styleName: Body CAPS 14;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;


				@media (max-width: 767px) {
				}
			}
		}

		&-address {
			color: #ababa7;
			font-family: "Questrial";
			font-size: 18px;
			font-weight: 400;
			line-height: 23px;
			letter-spacing: 0em;
			text-align: left;
			@media (max-width: 767px) {
				margin-bottom: 24px;
			}
		}
		&-first {
			display: flex;
			gap: 40px;
			text-transform: uppercase;

			flex-wrap: wrap;

			@media (min-width: 768px) and (max-width: 991px) {
				gap: 20px;
				max-width: 50vw;
			}
			@media (max-width: 767px) {
				flex-wrap: wrap;
			}
			&-icons {
				display: flex;
				gap: 16px;
				svg:hover {
					path {
						fill: #8C7E66;
					}
				}
				@media (max-width: 767px) {
					margin: auto;
					margin-bottom: 40px;
				}
			}
			&-design {
				color: #ababa7;
				a {
				text-transform: uppercase;
				}
			}
		}
		a {
			align-self: center;
			color: #ababa7;
			text-transform: uppercase;
		}

		svg {
			height: 24px;
			width: 24px;

			@media (max-width: 767px) {
				width: 40px;
				height: 40px;
			}
		}
	}
}
