.error404 {
	background: #161619 var(--url-noise) repeat center center;
	display: flex;
	flex-direction: column;
	height: calc(var(--vh, 1vh) * 100);
	width: 100vw;
	overflow: hidden;
	position: relative;

	&-container {
		margin-top: 100px;
		// position: relative;
		align-self: center;

		@media (max-width: 991px) {
			margin-top: auto;
		}

		&__left {
			&-box {
				position: absolute;
				left: 0;
				top: 226px;
                @media (max-width: 767px) and (max-height: 660px) {
                    left: -29vw !important;
                }
                @media (max-width: 767px) {
                    left: -60px;
                    top: 89px;

                    img {
                        height: 125px;
                        width: 162px;
                    }
                }
				.error404-container__box-line-bottom {
					width: calc(100% + 8px);
				}
			}
		}

		&__right {
			&-box {
				position: absolute;
				right: -0;
				top: 339px;
                @media (max-width: 767px) {
                    right: -70px;
                }
				@media (max-width: 900px) {
					top: 112px;
                    right: -10vw;
                    img {
                        height: 58.4vw;
						max-width: 219px;
                    }
				}
				.error404-container__box-line-bottom {
					width: calc(100% + 8px);
					right: 0px;
				}
			}
			&-text {
				top: 165px;
				position: absolute;
				right: 24px;
				color: #ababa7;
				max-width: 219px;
				//styleName: Body CAPS 14;
				font-family: "Questrial";
				font-size: 14px;
				font-weight: 400;
				line-height: 18px;
				letter-spacing: 0em;
				text-align: left;
				text-transform: uppercase;
                @media (max-width: 767px) {
                    max-width: 148px;
                    top: 70px;
                }
				@media (max-width: 900px) {
					top: 132px;
					right: 100px;
				}
                @media (max-width: 370px) {
                    right: 15%;
                }
			}
		}

		&__description {
			margin-top: 32px;
			width: 100%;
			text-align: center;
			//styleName: H4;
			font-family: "Glacior";
			font-size: 40px;
			font-weight: 400;
			line-height: 40px;
			letter-spacing: 0em;
			text-align: center;
			color: #ababa7;
			margin-right: auto;
			margin-left: auto;

			@media(min-width: 768px) {
				max-width: 600px;
			}

            @media (min-width: 768px) and (max-width: 991px) {
                position: absolute;
                top: 50%;
                right: 50%;
                transform: translate(50%, calc(50% + 128px));
            }

            @media (max-width: 767px) {
                position: absolute;
                top: 50%;
                transform: translate(50%, calc(-50% + 146px ));
                left: -50%;
                margin-top: 0;
            }

            @media (max-width: 360px) {
                font-size: 30px;
                line-height: 30px;
            }
		}

		&__goHome {
			color: #ababa7;
			margin: 24px auto 0 auto;
			text-transform: uppercase;
			display: block;

			&:hover {
				color: unset;
			}

            @media (max-width: 767px) {
                margin-bottom: 40px;
            }
            @media (min-width: 768px) and (max-width: 991px) {
                margin-bottom: 40px;
            }
		}

		&__box {
			position: relative;
			display: flex;
			justify-content: center;
			max-width: 688px;
			margin: auto;

            @media (max-width: 991px) {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

			&-line {
				&-topleft {
					position: absolute;
					top: -8px;
					width: calc(25%);
					left: -8px;
					height: 1px;
					background: #8c7e66;
					&::before {
						content: " ";
						background: #8c7e66;
						position: absolute;
						height: 5px;
						width: 5px;
						border-radius: 100%;
						right: 0;
						top: -2px;
					}
				}
				&-topright {
					position: absolute;
					top: -8px;
					width: calc(25%);
					height: 1px;
					right: -8px;
					background: #8c7e66;
					&::before {
						content: " ";
						background: #8c7e66;
						position: absolute;
						height: 5px;
						width: 5px;
						border-radius: 100%;
						left: 0;
						top: -2px;
					}
				}
				&-left {
					position: absolute;
					left: -8px;
					top: -8px;
					height: calc(100% + 16px);
					width: 1px;
					background: #8c7e66;
				}
				&-right {
					position: absolute;
					right: -8px;
					top: -8px;
					height: calc(100% + 16px);
					width: 1px;
					background: #8c7e66;
				}
				&-bottom {
					position: absolute;
					bottom: -8px;
					width: calc(100% + 16px);
					height: 1px;
					background: #8c7e66;
				}
			}

			background: #2c2c32;
			margin: 0 24px;
			padding: 56px 82px 88px 82px;
			margin: auto;
			@media (min-width: 768px) and (max-width: 1440px) {
				padding: 56px 42px 68px 42px;
				width: fit-content;
				margin: auto;
			}

            @media (max-width: 767px) {
                padding: 24px 30px 40px 30px;
                width: fit-content;
				margin: auto;
            }

            @media (max-width: 340px) {
                padding: 24px 20px 40px 20px;
            }

			&-title {
				font-family: "Glacior";
				font-size: 330px;
				font-weight: 400;
				line-height: 330px;
				letter-spacing: 0em;
				text-align: center;
				color: #ababa7;
				@media (min-width: 768px) and (max-width: 1200px) {
					font-size: 20vw;
					line-height: 20vw;
				}
				@media (max-width: 767px) {
					font-size: 96px;
					font-weight: 400;
					line-height: 96px;
					letter-spacing: 0em;
					text-align: center;
				}
			}
			&-underline {
				top: -40px;
				display: flex;
				justify-content: center;

				@media (max-width: 1200px) {
					top: -20px;
				}
				position: relative;
			}
			&-description {
				@media (min-width: 1200px) {
					width: 45.5vw;
				}
				max-width: 560px;
				text-align: center;
				margin: auto;

				font-family: "Questrial";
				font-size: 18px;
				font-weight: 400;
				line-height: 23px;
				letter-spacing: 0em;
				text-align: center;
				color: #ababa7;

				@media (max-width: 1120px) {
					font-size: 1.2rem;
				}
				@media (max-width: 767px) {
					//styleName: Body 18;
					font-family: "Questrial";
					font-size: 18px;
					font-weight: 400;
					line-height: 23px;
					letter-spacing: 0em;
					text-align: center;
				}
			}
			&-content {
				text-align: center;
				// left: 50%;
			}
		}
	}
}
